@import '../../utils/variables/style-variables';
#navbar {
	position: absolute;
	width: 60vw;
	height: 100px;
	display: flex;
	padding: 40px;
	margin: 32px;
	z-index: 10;
	align-items: center;
	a {
		color: $color-primary;
		text-decoration: none;
		font-size: 1.4rem;
		display: block;
		margin-left: 32px;
		cursor: pointer;
		position: relative;
		text-transform: lowercase;
		font-weight: 600;
		&::after {
			content: '';
			display: none;
			position: absolute;
			width: 50%;
			height: 2px;
			background-color: $color-primary;
			transform: translate(50%, 3px);
		}
		&:hover::after {
			display: block;
		}
	}
	img {
		transform: Scale(1.5);
		margin-right: 32px;
	}
}
